// 创建应用程序的路由器
import Vue from 'vue'
import VueRouter from 'vue-router'
// 此时就可以在Vue实例中配置路由器了
Vue.use(VueRouter)

// 引入组件
import Index from '../views/index'
import Login from '../views/login'

// 创建并暴露一个路由器
export default new VueRouter({
  mode:'history',
  routes: [
    {
      path: '/', redirect: '/login',
    },
    {
      path: '/login', component: Login
    },
    {
      path: '/code',
      component: Index,
      redirect: '/home',
      // 子路由
      children: [
        {
          path: '/home',
          name: 'home',
          component: () => import(('@/components/homePage/index'))
        },
        {
          path: '/project',
          name: 'project manage',
          component: () => import(('@/components/project'))
        },
        // {
        //   path: '/ddgl',
        //   name: 'ddgl',
        //   component: () => import(('@/components/ddgl'))
        // }
      ]
    },
  ]
})